<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <!-- <li class="collection-header"><h2>Kidz</h2> -->
      <div v-if="collection !== 'none'" class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
      <i class="fa fa-plus"></i>
      </router-link>
      </div>
      </li>
      <li v-for="kid in this.kidz" v-bind:key="kid.id" v-bind:class="[{checkedin: kid.in_out}, 'collection-item']">
        <div class="chip">{{kid.age}}</div><span class="name" v-on:click="checkInOut(kid.id, kid.in_out)">{{kid.name}}</span>
        <span class="phone">{{ kid.phone }}</span>
        <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {kid_id: kid.id, colName: collection}}">
          <i class="fa fa-eye fa-2x"></i>
        </router-link>
      </li>
    </ul>
    <div class="fix-action-btn">
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit.js";
import firebase from "firebase";
export default {
  name: "dashboard",
  data() {
    return {
      kidz: [],
      collection: this.$store.state.colName
    }
  },

  created() {
    db.collection(this.$store.state.colName)
      .orderBy("name")
      .onSnapshot(snap => {
        this.kidz = []
        var _this = this
        snap.forEach(doc => {
          const data = {
            id: doc.id,
            name: doc.data().name,
            in_out: doc.data().in_out,
            age: doc.data().age,
            phone: doc.data().phone
          }
          _this.kidz.push(data)
        })
      })
      this.$store.watch(
        (state) => {
      return state.colName
    },
    (val) => {
      db.collection(this.$store.state.colName)
      .orderBy("name")
      .onSnapshot(snap => {
        this.kidz = []
        var _this = this
        snap.forEach(doc => {
          const data = {
            id: doc.id,
            name: doc.data().name,
            in_out: doc.data().in_out,
            age: doc.data().age,
            phone: doc.data().phone
          }
          _this.kidz.push(data)
        })
        _this.collection = this.$store.state.colName
      })
    },
    {
      deep: true
    }
  )

  },
  methods: {
    checkInOut(id, inout) {
      db.collection(this.$store.state.colName)
        .doc(id)
        .update({ in_out: !inout })
        .then(docRef => {
          console.log("check in/out complete");
        });
    },
  },
  computed: {

  }
};
</script>

<style scoped>
#dashboard {
  max-width: 600px;                                 
  margin: 0 auto;
}

.checkedin {
  background-color: lightgreen
}

.checkedout {
  background-color: lightcoral
}

.name {
  font-size: 18px;
  font-weight: bold;
  margin-left: 1rem;
}

.phone {
  font-size: 12px;
  font-weight: 500;
  font-style: italic;
  margin: auto;
}

/*li.collection.item{
  padding: 10px 20px !important;
}*/
</style>



