<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">
        Grade: {{age}}
      </li>
      <li class="collection-item">
        In_Out: {{in_out}}
      </li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button v-on:click="deleteEmployee" class="btn red">Delete</button>
    <div class="fix-action-btn">
      <router-link v-bind:to="{name: 'edit-employee', params:{kid_id: this.id, colName: collection}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js';
import firebase from "firebase"
export default {
  name:'view-employee',
  data(){
    return{
      id: null,
      name: null,
      age: null,
      in_out: null,
      collection: this.$store.state.colName
    }
  },
beforeRouteEnter(to, from, next){
    db.collection(to.params.colName).where(firebase.firestore.FieldPath.documentId(), "==", to.params.kid_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.id = doc.id;
          vm.name = doc.data().name;
          vm.age = doc.data().age;
          vm.in_out = doc.data().in_out;
        });
      });
    });
  },
  watch: {
    '$route': 'fetchData'
  },

  methods: {
 fetchData: function () {
      db.collection(this.$store.state.colName).where(firebase.firestore.FieldPath.documentId(), "==", this.$route.params.kid_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.id = doc.id;
          this.name = doc.data().name;
          this.age = doc.data().age;
          this.in_out = doc.data().in_out;
        });
      });
   },
  deleteEmployee: function () {
        db.collection(this.$store.state.colName).where(firebase.firestore.FieldPath.documentId(), "==", this.$route.params.kid_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete();
            this.$router.push('/');
          });
        });
    }
  }
}
</script>

<style scoped>
.fix-action-btn{
  margin-top: 10px;
}
</style>




