<template>
  <div id="edit-employee">
    <div class="row">
      <form v-on:submit.prevent="updateEmployee" class="col s12">

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
          </div>          
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="age" required>
          </div>          
        </div>


        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js';
import firebase from 'firebase'
export default {
  name:'edit-employee',
  data(){
    return{
      id: null,
      name: null,
      age: null,
      in_out: null,
      kid_id: ''
    }
  },
beforeRouteEnter(to, from, next){
    db.collection(to.params.colName).where(firebase.firestore.FieldPath.documentId(), "==", to.params.kid_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.id = doc.id;
          vm.name = doc.data().name;
          vm.age = doc.data().age;
          vm.in_out = doc.data().in_out;
        });
      });
    });
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
 fetchData: function () {
      db.collection(this.$route.params.colName).where(firebase.firestore.FieldPath.documentId(), "==", this.$route.params.kid_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.id = doc.id;
          this.name = doc.data().name;
          this.age = doc.data().age;
          this.in_out = doc.data().in_out;
        });
      });
    },
    updateEmployee: function () {
      db.collection(this.$route.params.colName).where(firebase.firestore.FieldPath.documentId(), "==", this.$route.params.kid_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            //employee_id: this.employee_id,
            name: this.name,
            age: this.age,
            in_out: this.in_out
          }).then(() => {
            this.$router.push({name:'view-employee', params:{kid_id: this.id, colName: this.$route.params.colName}});
          });
        });
      });
    }
  }
}
</script>


