<template>
  <div id="new-employee">
    <h3>New</h3>
    <div class="row">
      <form v-on:submit.prevent="saveEmployee" class="col s12">

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label>Name</label>
          </div>          
        </div>

          <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="phone">
            <label>Phone Number</label>
          </div>          
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="age">
            <label>Grade</label>
          </div>          
        </div>

        <br/>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js';
export default {
  name:'new-employee',
  data(){
    return{
      id: null,
      name: null,
      phone: null,
      age: null,
      in_out: null
    }
  },
  methods: {
    // Save the new employee to the DB
    saveEmployee: function () {
      db.collection(this.$store.state.colName).add({
        name: this.name,
        age: this.age,
        in_out: false,
        phone: this.phone
      }).then(docRef => this.$router.push('/')).catch(error => console.log(err));
    }
  }
}
</script>


