<template>
  <nav class="nav-extended">
    <div class="nav-wrapper green">
      <div class="container">
        <a class="brand-logo right">Kidz</a>
      <ul class="left">
        <li v-on:click="colClass" ><a>Classes</a></li>
        <li v-on:click="colBus" ><a>Bus</a></li>
        <li v-on:click="colVan" ><a>Van</a></li>
      </ul>
        </div>

      </div>      
    </div>
  </nav>
</template>

<script>
export default {
  data(){
    return{
      
    }
  },
methods: {
  colClass() {
    this.$store.commit('switchCol', 'kidz')
  },
    colBus() {
    this.$store.commit('switchCol', 'kidz_bus')
  },
  colVan() {
    this.$store.commit('switchCol', 'kidz_van')
  }

}
}
</script>


